.benefits-list {
  display: grid;
  grid-template-columns: repeat(3, minmax(0, 1fr));
  gap: 1.5rem;
  padding: 12vh 1.25rem 2rem; /* ìƒë‹¨ ê³ ì • í—¤ë”(10vh) ê³ ë ¤í•˜ì—¬ ì—¬ìœ  íŒ¨ë”© */
  box-sizing: border-box;
}

@media (max-width: 1200px) {
  .benefits-list {
    grid-template-columns: repeat(2, minmax(0, 1fr));
  }
}

@media (max-width: 640px) {
  .benefits-list {
    grid-template-columns: 1fr;
    padding-top: 11vh;
  }
}

.benefits-frame {
  position: relative;
  padding: 14px;
  border-radius: 16px;
  background: linear-gradient(135deg, #e5e7eb, #ffffff);
  box-shadow: 0 10px 30px rgba(0,0,0,0.08), inset 0 0 0 1px rgba(0,0,0,0.04);
}

.benefits-frame::before {
  content: '';
  position: absolute;
  inset: 6px;
  border-radius: 12px;
  border: 2px solid rgba(0,0,0,0.06);
  pointer-events: none;
}

.benefits-frame-inner {
  background: #ffffff;
  border-radius: 12px;
  padding: 14px;
  box-shadow: inset 0 0 0 1px #f1f5f9, 0 1px 1px rgba(0,0,0,0.02);
}

/* ì¹´ë“œ ë‚´ë¶€: ì¢Œì¸¡ ì§€ë„ / ìš°ì¸¡ ì •ë³´ */
.benefits-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1.25rem;
  width: 100%;
  height: 380px; /* ì¹´ë“œ ë†’ì´ ê³ ì • */
  padding: 0.75rem;
  box-sizing: border-box;
}

.benefits-map {
  width: 100%;
  height: 100%;
  min-height: 260px;
  border-radius: 12px;
  border: 1px solid #e5e7eb;
  background: #f8fafc;
}

.benefits-info {
  width: 100%;
  height: 100%;
  min-height: 260px;
  border-radius: 12px;
  border: 1px solid #e5e7eb;
  background: #ffffff;
  padding: 1rem;
  box-sizing: border-box;
  display: grid;           /* ê·¸ë¦¬ë“œ ì»¨í…ìŠ¤íŠ¸ë¡œ ì •ë ¬ */
  grid-auto-rows: min-content;
  align-content: start;
  gap: 0.5rem;
  overflow: hidden;        /* ì˜ì—­ ë°–ìœ¼ë¡œëŠ” í‘œì‹œí•˜ì§€ ì•ŠìŒ(ìŠ¤í¬ë¡¤ ì œê±°) */
  min-width: 0;
