.benefits-title {
  margin: 0;
  font-size: 1.25rem;
  font-weight: 800;
  color: #0f172a;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.benefits-row {
  display: grid;
  grid-template-columns: 80px 1fr;
  gap: 0.5rem;
  align-items: center;
  min-width: 0;
}

.benefits-row .label {
  color: #475569;
  font-weight: 700;
}

.benefits-row .value {
  color: #0f172a;
  text-decoration: none;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  min-width: 0;
}

.benefits-divider {
  height: 1px;
  background: #e5e7eb;
  margin: 0.25rem 0 0.15rem;
}

.benefits-subtitle {
  color: #0f172a;
  font-weight: 800;
}

