  .nav-btn {
    width: 40px;
    height: 40px;
    border-radius: 100%;
    border: 1px solid #d1d5db;
    background: #f8fafc;
    cursor: pointer;
    font-size: 1.25rem;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    margin: 0 atuo;
